/* Advanced Custom Animations & Effects */

/* Sophisticated Card Hover Effects */
.card-3d-hover {
  transform-style: preserve-3d;
  transition: transform 0.6s cubic-bezier(0.23, 1, 0.32, 1);
}

.card-3d-hover:hover {
  transform: perspective(1000px) rotateY(2deg) rotateX(-2deg) translateZ(10px);
}

/* Magnetic Button Effect */
.btn-magnetic {
  transition: all 0.3s cubic-bezier(0.23, 1, 0.32, 1);
}

/* Ripple Effect */
@keyframes ripple {
  0% {
    transform: scale(0);
    opacity: 1;
  }
  100% {
    transform: scale(4);
    opacity: 0;
  }
}

.ripple-effect {
  position: relative;
  overflow: hidden;
}

.ripple-effect::before {
  content: '';
  position: absolute;
  top: 50%;
  left: 50%;
  width: 20px;
  height: 20px;
  background: rgba(255, 255, 255, 0.5);
  border-radius: 50%;
  transform: translate(-50%, -50%) scale(0);
  pointer-events: none;
}

.ripple-effect:active::before {
  animation: ripple 0.6s ease-out;
}

/* Parallax Scroll Effect */
.parallax-slow {
  transition: transform 0.5s cubic-bezier(0.23, 1, 0.32, 1);
}

/* Gradient Animation */
@keyframes gradientShift {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.animated-gradient {
  background: linear-gradient(270deg, #DC2626, #EF4444, #F87171, #DC2626);
  background-size: 400% 400%;
  animation: gradientShift 15s ease infinite;
}

/* Glowing Borders */
@keyframes borderGlow {
  0%, 100% {
    box-shadow: 0 0 5px rgba(220, 38, 38, 0.2), 0 0 10px rgba(220, 38, 38, 0.1);
  }
  50% {
    box-shadow: 0 0 20px rgba(220, 38, 38, 0.4), 0 0 30px rgba(220, 38, 38, 0.2), 0 0 40px rgba(220, 38, 38, 0.1);
  }
}

.glow-border {
  animation: borderGlow 3s ease-in-out infinite;
}

/* Floating Elements */
@keyframes float {
  0%, 100% {
    transform: translateY(0px) rotate(0deg);
  }
  33% {
    transform: translateY(-20px) rotate(2deg);
  }
  66% {
    transform: translateY(-10px) rotate(-2deg);
  }
}

.float-animation {
  animation: float 6s ease-in-out infinite;
}

/* Text Reveal Animation */
@keyframes textReveal {
  0% {
    opacity: 0;
    transform: translateY(20px) scale(0.95);
  }
  100% {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

.text-reveal {
  animation: textReveal 0.8s cubic-bezier(0.23, 1, 0.32, 1) forwards;
}

/* Staggered Fade In */
.stagger-fade-in > * {
  opacity: 0;
  animation: fadeInStagger 0.6s ease-out forwards;
}

.stagger-fade-in > *:nth-child(1) { animation-delay: 0.1s; }
.stagger-fade-in > *:nth-child(2) { animation-delay: 0.2s; }
.stagger-fade-in > *:nth-child(3) { animation-delay: 0.3s; }
.stagger-fade-in > *:nth-child(4) { animation-delay: 0.4s; }
.stagger-fade-in > *:nth-child(5) { animation-delay: 0.5s; }
.stagger-fade-in > *:nth-child(6) { animation-delay: 0.6s; }

@keyframes fadeInStagger {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Pulsating Dot */
@keyframes pulseDot {
  0%, 100% {
    transform: scale(1);
    opacity: 1;
  }
  50% {
    transform: scale(1.5);
    opacity: 0.5;
  }
}

.pulse-dot {
  animation: pulseDot 2s ease-in-out infinite;
}

/* Slide In Animations */
@keyframes slideInRight {
  from {
    transform: translateX(100px);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

@keyframes slideInLeft {
  from {
    transform: translateX(-100px);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

.slide-in-right {
  animation: slideInRight 0.7s cubic-bezier(0.23, 1, 0.32, 1) forwards;
}

.slide-in-left {
  animation: slideInLeft 0.7s cubic-bezier(0.23, 1, 0.32, 1) forwards;
}

/* Morphing Background */
@keyframes morphBackground {
  0%, 100% {
    border-radius: 60% 40% 30% 70% / 60% 30% 70% 40%;
  }
  50% {
    border-radius: 30% 60% 70% 40% / 50% 60% 30% 60%;
  }
}

.morph-blob {
  animation: morphBackground 10s ease-in-out infinite;
}

/* Spinning Loader */
@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.spin-slow {
  animation: spin 3s linear infinite;
}

/* Scale Pulse */
@keyframes scalePulse {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
}

.scale-pulse {
  animation: scalePulse 2s ease-in-out infinite;
}

/* Bounce In */
@keyframes bounceIn {
  0% {
    opacity: 0;
    transform: scale(0.3) translateY(-50px);
  }
  50% {
    opacity: 1;
    transform: scale(1.05);
  }
  70% {
    transform: scale(0.9);
  }
  100% {
    transform: scale(1);
  }
}

.bounce-in {
  animation: bounceIn 0.8s cubic-bezier(0.68, -0.55, 0.265, 1.55) forwards;
}

/* Custom Scrollbar Enhancements */
.custom-scrollbar::-webkit-scrollbar {
  width: 6px;
  height: 6px;
}

.custom-scrollbar::-webkit-scrollbar-track {
  background: rgba(241, 245, 249, 0.3);
  border-radius: 10px;
}

.custom-scrollbar::-webkit-scrollbar-thumb {
  background: linear-gradient(180deg, #DC2626, #B91C1C);
  border-radius: 10px;
  -webkit-transition: background 0.3s ease;
  transition: background 0.3s ease;
}

.custom-scrollbar::-webkit-scrollbar-thumb:hover {
  background: linear-gradient(180deg, #B91C1C, #991B1B);
}

/* Spotlight Effect */
.spotlight {
  position: relative;
  overflow: hidden;
}

.spotlight::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: radial-gradient(circle, rgba(255,255,255,0.15) 0%, transparent 70%);
  opacity: 0;
  transition: opacity 0.5s ease;
  pointer-events: none;
}

.spotlight:hover::before {
  opacity: 1;
}

/* Tilt Effect */
.tilt-effect {
  transition: transform 0.3s ease;
}

.tilt-effect:hover {
  transform: perspective(1000px) rotateX(5deg) rotateY(-5deg);
}

/* Badge Pulse */
@keyframes badgePulse {
  0%, 100% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.1);
  }
}

.badge-pulse {
  animation: badgePulse 2s ease-in-out infinite;
}

/* Typing Cursor */
.typing-cursor::after {
  content: '|';
  animation: blink 1s step-end infinite;
  color: #DC2626;
}

@keyframes blink {
  0%, 50% {
    opacity: 1;
  }
  51%, 100% {
    opacity: 0;
  }
}

/* Smooth Color Transitions */
.smooth-color {
  transition: color 0.4s cubic-bezier(0.4, 0, 0.2, 1),
              background-color 0.4s cubic-bezier(0.4, 0, 0.2, 1),
              border-color 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}

/* Image Zoom on Hover */
.image-zoom {
  transition: transform 0.7s cubic-bezier(0.4, 0, 0.2, 1);
}

.image-zoom:hover {
  transform: scale(1.15);
}

/* Slide Up on Scroll */
@keyframes slideUpFade {
  from {
    opacity: 0;
    transform: translateY(60px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.scroll-reveal {
  animation: slideUpFade 0.8s cubic-bezier(0.23, 1, 0.32, 1) forwards;
}

/* Neon Glow Effect */
@keyframes neonGlow {
  0%, 100% {
    text-shadow: 0 0 10px rgba(220, 38, 38, 0.5),
                 0 0 20px rgba(220, 38, 38, 0.3),
                 0 0 30px rgba(220, 38, 38, 0.2);
  }
  50% {
    text-shadow: 0 0 20px rgba(220, 38, 38, 0.8),
                 0 0 30px rgba(220, 38, 38, 0.6),
                 0 0 40px rgba(220, 38, 38, 0.4),
                 0 0 50px rgba(220, 38, 38, 0.2);
  }
}

.neon-text {
  animation: neonGlow 2s ease-in-out infinite;
}

/* Loading Skeleton */
@keyframes skeletonLoading {
  0% {
    background-position: -200% 0;
  }
  100% {
    background-position: 200% 0;
  }
}

.skeleton {
  background: linear-gradient(
    90deg,
    #f0f0f0 25%,
    #e0e0e0 50%,
    #f0f0f0 75%
  );
  background-size: 200% 100%;
  animation: skeletonLoading 1.5s ease-in-out infinite;
}

/* Underline Animation */
.animated-underline {
  position: relative;
  display: inline-block;
}

.animated-underline::after {
  content: '';
  position: absolute;
  width: 0;
  height: 2px;
  bottom: -4px;
  left: 50%;
  background-color: #DC2626;
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
  transform: translateX(-50%);
}

.animated-underline:hover::after {
  width: 100%;
}

/* Gradient Text */
.gradient-text {
  background: linear-gradient(135deg, #DC2626 0%, #F87171 50%, #DC2626 100%);
  background-size: 200% auto;
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
  animation: gradientShift 3s ease infinite;
}

/* Shadow Lift on Hover */
.shadow-lift {
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.shadow-lift:hover {
  transform: translateY(-4px);
  box-shadow: 0 20px 40px -10px rgba(220, 38, 38, 0.2);
}

/* Breathing Animation */
@keyframes breathe {
  0%, 100% {
    transform: scale(1);
    opacity: 0.8;
  }
  50% {
    transform: scale(1.05);
    opacity: 1;
  }
}

.breathe {
  animation: breathe 4s ease-in-out infinite;
}

/* Animated Gradient Background */
.animate-gradient {
  background-size: 200% 200%;
  animation: gradientMove 3s ease infinite;
}

@keyframes gradientMove {
  0%, 100% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
}

